import { SET_SELECTED_MENU, SET_SELECTED_LANDING_MENU, SET_MENU, SET_LANDING_MENU } from '../constants/types';
import { LANDING } from '../constants/iapplyApps';
import { setApplyApp } from './iapply';
import menuContent from '../constants/menuContent';

function onSideBarMenuBack(item) {
  return function(dispatch) {
    dispatch({ type: SET_MENU, content: menuContent[item] });

    let defaultItems = menuContent.quickLinks.content[0].content.map(x => ({ ...x, source: LANDING }));
    dispatch({ type: SET_LANDING_MENU, content: { content: defaultItems, context: LANDING } });
    dispatch({ type: SET_SELECTED_LANDING_MENU, content: defaultItems[0] });
  };
}
function onSideBarMenuSelected(item) {
  return function(dispatch, getState) {
    // remove app when main links are invoked
    dispatch(setApplyApp(null));

    //update menu content state
    if (item) {
      const state = getState();
      menuContent[state.nav.menu.id].selected = item;
    }

    //swtich if iapply
    if (item.id === 7) {
      dispatch({ type: SET_MENU, content: menuContent.iapply });
    } else {
      dispatch({ type: SET_SELECTED_MENU, item });
      dispatch({ type: SET_SELECTED_LANDING_MENU, content: item });
    }
  };
}

module.exports = { onSideBarMenuSelected, onSideBarMenuBack };
